import { BrowserRouter, Route, Routes, useNavigate } from "react-router-dom";
import AppRoutes from "./route";
import { useEffect } from "react";
import AppNotification from "./helpers/AppNotification";
import DeviceModel from "./models/DeviceModel";
import AppCookie from "./helpers/AppCookie";
import AuthMoldel from "./models/AuthMoldel";
import Swal from 'sweetalert2';

function App() {
  // ly thng tin my v trnh duyt
  const browserInfo = navigator.userAgent;
  console.log("Thng tin trnh duyt:", browserInfo);
  
  useEffect(() => {
    const intervalId = setInterval(() => {
        var isLoggedin = AuthMoldel.checkAuth();
          if (isLoggedin) {
          // ly thng tin my v trnh duyt
          const browserInfo = navigator.userAgent;
          DeviceModel.getDevice({
            browserInfo: browserInfo
          })
            .then(res => {
              if (res.success === false) {
                AuthMoldel.logout({
                  browserInfo: browserInfo
                }).then((res) => {
                  AppCookie.deleteCookie('token');
                  AppCookie.deleteCookie('user');
                  window.location.href = '/login';
                }).catch((err) => {
                  console.log(err);
                });
    
                Swal.fire({
                  icon: "error",
                  title: "Ti Khon  ng nhp trn thit b khc",
                  showConfirmButton: false,
                  timer: 2000,
                });
                clearInterval(intervalId);
              }
            }).catch(err => {
              console.log(err);
              AppNotification.showError('Ly d liu tht bi!');
            })
          }
        }, 5000); // Gi hm mi 5 giy
    
        return () => clearInterval(intervalId); // Xa interval khi component unmount 
    });
  
  return (
    <AppRoutes />
  );
}

export default App;
