import * as Yup from 'yup';
class FormCreateBillSchema {
    constructor() {
        this.type = '';
    }

    generateSchema(type) {
        let schema;
        if (type === "MB" || type === "ACB" || type === "ICB" || type === "TPB") {
            schema = Yup.object().shape({
                amount: Yup.string().required('S tin khng c  trng'),
                hour: Yup.string().required('Gi khng c  trng'),
                day: Yup.string().required('Ngy khng c  trng'),
                receiver_name: Yup.string().required('Tn ngi nhn khng c  trng').matches(/^[A-Z\s]+$/, 'Tn ngi nhn ch c cha k t vit hoa v khong trng'),
                receiver_number: Yup.string().required('S ti khon ngi nhn khng c  trng').matches(/^[0-9]+$/, 'S ti khon ngi nhn ch c cha cc k t t 0 n 9'),
                receiver_bank_name: Yup.string().required('Tn ngn hng ngi nhn khng c  trng'),
                transaction_code: Yup.string().required('M giao dch khng c  trng'),
                operatingSystem: Yup.string().required('H iu hnh khng c  trng'),
                decreption: Yup.string()
                    .required('M t khng c  trng')
                    .max(150, 'M t khng c vt qu 150 k t')
                    .matches(/^[\w\s]*$/, 'M t khng c cha k t c bit, ch c cha ch, s v khong trng'),
                sender_name: Yup.string().required('S tin khng c  trng').matches(/^[A-Z\s]+$/, 'Tn ngi nhn ch c cha k t vit hoa v khong trng'),
                sender_number: Yup.string().required('khng c  trng').matches(/^[0-9]+$/, 'S ti khon ngi nhn ch c cha cc k t t 0 n 9'),
            });
        } else if (type === "MSB") {
            schema = Yup.object().shape({
                amount: Yup.string().required('S tin khng c  trng'),
                hour: Yup.string().required('Gi khng c  trng'),
                day: Yup.string().required('Ngy khng c  trng'),
                receiver_name: Yup.string().required('Tn ngi nhn khng c  trng').matches(/^[A-Z\s]+$/, 'Tn ngi nhn ch c cha k t vit hoa v khong trng'),
                receiver_number: Yup.string().required('S ti khon ngi nhn khng c  trng').matches(/^[0-9]+$/, 'S ti khon ngi nhn ch c cha cc k t t 0 n 9'),
                receiver_bank_name: Yup.string().required('Tn ngn hng ngi nhn khng c  trng'),
                transaction_code: Yup.string().required('M giao dch khng c  trng'),
                operatingSystem: Yup.string().required('H iu hnh khng c  trng'),
                decreption: Yup.string()
                    .required('M t khng c  trng')
                    .max(150, 'M t khng c vt qu 150 k t')
                    .matches(/^[\w\s]*$/, 'M t khng c cha k t c bit, ch c cha ch, s v khong trng'),
                sender_name: Yup.string().required('S tin khng c  trng')
                    .matches(/^[A-Z\s]+$/, 'Tn ngi nhn ch c cha k t vit hoa v khong trng'),
            });
        } else {
            schema = Yup.object().shape({
                amount: Yup.string().required('S tin khng c  trng'),
                hour: Yup.string().required('Gi khng c  trng'),
                day: Yup.string().required('Ngy khng c  trng'),
                receiver_name: Yup.string()
                    .required('Tn ngi nhn khng c  trng')
                    .matches(/^[A-Z\s]+$/, 'Tn ngi nhn ch c cha k t vit hoa v khong trng'),
                receiver_number: Yup.string().required('S ti khon ngi nhn khng c  trng').matches(/^[0-9]+$/, 'S ti khon ngi nhn ch c cha cc k t t 0 n 9'),
                receiver_bank_name: Yup.string().required('Tn ngn hng ngi nhn khng c  trng'),
                transaction_code: Yup.string().required('M giao dch khng c  trng'),
                operatingSystem: Yup.string().required('H iu hnh khng c  trng'),
                decreption: Yup.string()
                    .required('M t khng c  trng')
                    .max(150, 'M t khng c vt qu 150 k t')
                    .matches(/^[\w\s]*$/, 'M t khng c cha k t c bit, ch c cha ch, s v khong trng'),
            });
        }
        return schema
    }
}
export default new FormCreateBillSchema;